import { FunctionComponent, Fragment } from 'react';
import { useTranslation } from 'react-i18next';

import { Accordion, Card, Col, Row } from 'react-bootstrap';
import { TwitterVideoEmbed } from 'react-twitter-embed';

import { chunkArrayInGroups, getDescription } from './../utils';

import sing_lists from '../assets/sings.json';

interface Sing {
  name: string;
  url: string;
  urls?: { [index: string]: any; };
  type: string;
  description: { [index: string]: any; };
};

const Sings: FunctionComponent = () => {
  const { i18n } = useTranslation();
  const currentLng: string = `${i18n.language}`;
  const youtubeSings: Sing[] = sing_lists.filter(s => s.type === 'youtube');
  const rowYoutubeSings: Sing[][] = chunkArrayInGroups(youtubeSings, 3);
  const twitterSings: Sing[] = sing_lists.filter(s => s.type === 'twitter');

  return (
    <Fragment>
      <Accordion defaultActiveKey="0" className="mt-2 mb-2">
        <Card>
          <Accordion.Toggle as={Card.Header} eventKey="0">
            <h5>Singsüé§üé∂üéµ</h5>
          </Accordion.Toggle>
          <Accordion.Collapse eventKey="0">
            <Card.Body>
              {
                rowYoutubeSings.map((sings: Sing[], index: number) => (
                  <Row key={index}>
                  {sings.map((sing: Sing) => (
                    <Col xs={12} md={4} key={sing.name}>
                      <div className="embed-responsive embed-responsive-16by9">
                        <iframe title={getDescription(sing.description, currentLng)} className="embed-responsive-item" src={sing.url.replace("watch?v=", "embed/").replace('youtube.com', 'youtube-nocookie.com')} allowFullScreen></iframe>
                      </div>
                    </Col>
                  ))}
                  </Row>
                ))
              }
              <Row>
                {twitterSings.map((sing: Sing) => (
                  <Col xs={12} md={4} key={sing.name}>
                    <div className="embed-responsive" style={{'min-width': '250px'} as React.CSSProperties}>
                      <TwitterVideoEmbed id={getDescription(sing.urls, currentLng)} placeholder={'loading'} options={{dnt: false, width: 250 }} />
                    </div>
                  </Col>
                ))}
              </Row>
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </Accordion>
    </Fragment>
  );
};

export default Sings;
