import { FunctionComponent, Fragment } from 'react';
import { useTranslation } from 'react-i18next';
import { Accordion, Card, Row, Col } from 'react-bootstrap';

import { chunkArrayInGroups, getDescription } from './../utils';

import clip_lists from '../assets/clips.json';

interface Clip {
  name: string;
  url: string;
  description: { [index: string]: any; };
  source: string;
};

const Clips: FunctionComponent = () => {
  const { i18n } = useTranslation();
  const currentLng: string = `${i18n.language}`;
  const rowClips: Clip[][] = chunkArrayInGroups(clip_lists, 3);

  return (
    <Fragment>
      <Accordion defaultActiveKey="0" className="mt-2 mb-2">
        <Card>
          <Accordion.Toggle as={Card.Header} eventKey="0">
            <h5>ClipsüéûÔ∏è</h5>
          </Accordion.Toggle>
          <Accordion.Collapse eventKey="0">
            <Card.Body>
              {
                rowClips.map((clips: Clip[], index: number) => (
                  <Row key={index}>
                  {clips.map((clip: Clip) => (
                    <Col xs={12} md={4} key={clip.name}>
                      <div className="embed-responsive embed-responsive-16by9">
                        <iframe title={getDescription(clip.description, currentLng)} className="embed-responsive-item" src={clip.url.replace("watch?v=", "embed/").replace('youtube.com', 'youtube-nocookie.com')} allowFullScreen></iframe>
                      </div>
                    </Col>
                  ))}
                  </Row>
                ))
              }
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </Accordion>
    </Fragment>
  );
};

export default Clips;
